/* Стили навигации */
.navigation.historical {
    margin: 20px auto;
    background-color: var(--color-bg-alt);
    padding: 10px 20px;
    border-radius: 10px;
    text-align: center;
    font-weight: bold;
    color: var(--color-text-alt);
    font-family: 'Times New Roman', Times, serif;
}

.nav-list {
    list-style: none;
    display: flex;
    justify-content: space-around;
    padding: 0;
    margin: 0;
}

.nav-list li {
    margin: 0 10px;
}

.nav-list a {
    padding: 10px 20px;
    background-color: var(--color-button-bg);
    color: var(--color-text-alt);
    border-radius: 10px;
    cursor: pointer;
    transition: all 0.3s ease;
    outline: none;
    font-size: 16px;
    font-weight: bold;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 2px 6px var(--color-shadow);
    text-decoration: none;
    border: 2px solid transparent;
}

.nav-list a:hover, .nav-list a:focus {
    background-color: var(--color-button-hover);
    color: #F7E1C8;
    border-color: #F7E1C8;
}

.nav-list a:active, .nav-list a.active {
    color: var(--color-bg-alt);
    background: linear-gradient(145deg, rgba(20, 20, 20, 0.9), rgba(35, 35, 35, 0.9));
}

.icon-search::before { content: "🔍"; margin-right: 5px; }
.icon-gallery::before { content: "🖼️"; margin-right: 5px; }
.icon-podcast::before { content: "🎙️"; margin-right: 5px; }
.icon-article::before { content: "📰"; margin-right: 5px; }
.icon-map::before { content: "🗺️"; margin-right: 5px; }

/* Стили для кнопки мобильного меню */
.mobile-menu-toggle {
    display: none;
    position: fixed;
    top: 20px;
    right: 20px;
    background-color: var(--color-button-bg);
    color: var(--color-text-alt);
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    font-size: 18px;
    cursor: pointer;
}
